/* --------------------------------------------------- *
 * 1. GLOBAL
/* --------------------------------------------------- */
body, html {
  height: 100%; }

body {
  color: #161616;
  font-family: "Roboto", sans-serif;
  line-height: 1.65; }

body {
  display: grid;
  grid-template-rows: auto;
  width: 1170px;
  margin: 0 auto; }
  @media (max-width: 1280px) {
    body {
      width: 1050px; } }
  @media (max-width: 1150px) {
    body {
      width: 995px; } }
  @media (max-width: 995px) {
    body {
      width: 767px; } }
  @media (max-width: 767px) {
    body {
      width: 90%; } }

.site-header,
.site-footer {
  grid-column: 1 / 3; }

b, strong {
  color: #161616; }

a {
  color: #161616;
  text-decoration: none;
  transition: ease .3s; }
  a:hover, a:focus {
    text-decoration: none; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  color: #161616;
  word-wrap: break-word; }

h1 {
  font-size: 42px; }
  @media (max-width: 1280px) {
    h1 {
      font-size: 38px; } }

h2 {
  font-size: 36px; }
  @media (max-width: 1280px) {
    h2 {
      font-size: 24px; } }

p {
  margin: 0; }

iframe {
  border: none;
  width: 100%; }

img {
  max-width: 100%; }

ul, ol {
  list-style-position: outside;
  list-style-type: circle;
  margin: 0;
  padding: 0 0 0 20px; }

/* --------------------------------------------------- *
 * 2. SITE HEADER
/* --------------------------------------------------- */
.site-header {
  padding-top: 400px; }
  @media (max-width: 1280px) {
    .site-header {
      padding-top: 120px; } }
  @media (max-width: 767px) {
    .site-header {
      padding-top: 40px; } }

.site-header-title {
  text-transform: uppercase;
  margin: 0;
  line-height: 1; }
  .site-header-title + .site-header-subtitle {
    margin-top: 20px; }
    @media (max-width: 1280px) {
      .site-header-title + .site-header-subtitle {
        margin-top: 10px; } }

.site-header-subtitle {
  margin: 0;
  line-height: 1.2; }
  @media (max-width: 767px) {
    .site-header-subtitle {
      font-size: 21px; } }
  .site-header-subtitle + p {
    margin-top: 20px; }
    @media (max-width: 1280px) {
      .site-header-subtitle + p {
        margin-top: 10px; } }

.site-header p {
  font-weight: 300;
  line-height: 1.2; }
  @media (max-width: 767px) {
    .site-header p {
      font-size: 14px; } }

/* --------------------------------------------------- *
 * 3. SITE MAIN
/* --------------------------------------------------- */
.site-header + .site-main {
  margin-top: 230px; }
  @media (max-width: 1280px) {
    .site-header + .site-main {
      margin-top: 75px; } }
  @media (max-width: 767px) {
    .site-header + .site-main {
      margin-top: 40px; } }

