.site-main-about-profile {
  grid-area: box-1; }

.site-main-about-img {
  grid-area: box-2; }

.site-main-about-capabilities {
  grid-area: box-3; }

.site-main-about-cta {
  grid-area: box-4; }

.site-main-about-title {
  margin: 0 0 20px;
  line-height: 1; }
  @media (max-width: 767px) {
    .site-main-about-title {
      font-size: 21px; } }

.site-main-about-subtitle {
  margin: 0 0 10px;
  line-height: 1; }
  @media (max-width: 767px) {
    .site-main-about-subtitle {
      font-size: 21px; } }

@media (max-width: 767px) {
  .site-main-about-profile {
    margin-bottom: 20px; } }

.site-main-about-profile p {
  font-weight: 300; }
  @media (max-width: 767px) {
    .site-main-about-profile p {
      font-size: 16px; } }

.site-main-about-capabilities .list-about-capabilities {
  list-style: none outside none;
  margin: 0;
  padding: 0; }
  .site-main-about-capabilities .list-about-capabilities li {
    display: block;
    font-weight: 300; }
    @media (max-width: 767px) {
      .site-main-about-capabilities .list-about-capabilities li {
        font-size: 16px; } }

.site-main-about-img img {
  width: 100%; }

.site-main-about-cta {
  margin-top: 120px;
  font-weight: 300;
  text-align: center; }
  @media (max-width: 1280px) {
    .site-main-about-cta {
      margin-top: 40px; } }
  @media (max-width: 767px) {
    .site-main-about-cta {
      margin-top: 0;
      text-align: left;
      font-size: 14px; } }
  .site-main-about-cta a {
    color: #2937f0;
    font-weight: 400; }
    .site-main-about-cta a:hover, .site-main-about-cta a:focus {
      text-decoration: underline; }

.site-main-about + .site-main-portfolio {
  margin-top: 140px; }
  @media (max-width: 1280px) {
    .site-main-about + .site-main-portfolio {
      margin-top: 75px; } }

.site-main-portfolio {
  display: grid;
  grid-gap: 15px;
  grid-template-columns: 1fr 1fr; }
  @media (max-width: 690px) {
    .site-main-portfolio {
      grid-template-columns: 1fr; } }

.portfolio-item {
  margin-bottom: 140px; }
  @media (max-width: 1280px) {
    .portfolio-item {
      margin-bottom: 20px; } }
  @media (max-width: 690px) {
    .portfolio-item {
      grid-column: 1; } }
  .portfolio-item-link {
    background: #161616;
    display: block;
    backface-visibility: hidden;
    padding: 0; }
    .portfolio-item-link:hover img, .portfolio-item-link:focus img {
      opacity: 0.3;
      -ms-filter: "alpha(opacity=” $value * 100 “)";
      filter: alpha(opacity=30);
      zoom: 1; }
    .portfolio-item-link img {
      margin: 0;
      width: 100%;
      transition: all 350ms ease;
      backface-visibility: hidden;
      display: block; }
  .portfolio-item .portfolio-item-link + .portfolio-item-header {
    margin-top: 20px; }
  .portfolio-item-title {
    margin: 0; }
    @media (max-width: 767px) {
      .portfolio-item-title {
        font-size: 21px; } }
  .portfolio-item-category {
    font-weight: 300;
    display: block; }
    @media (max-width: 767px) {
      .portfolio-item-category {
        font-size: 16px; } }
    .portfolio-item-category a:hover, .portfolio-item-category a:focus {
      text-decoration: underline; }
  .portfolio-item-niche a {
    font-weight: 300i;
    font-style: italic;
    color: #969696; }
    @media (max-width: 767px) {
      .portfolio-item-niche a {
        font-size: 12px; } }
    .portfolio-item-niche a:hover, .portfolio-item-niche a:focus {
      text-decoration: underline; }

/* --------------------------------------------------- *
 * 4. SITE FOOTER
/* --------------------------------------------------- */
.site-main + .site-footer {
  margin-top: 160px; }
  @media (max-width: 1280px) {
    .site-main + .site-footer {
      margin-top: 90px; } }
  @media (max-width: 767px) {
    .site-main + .site-footer {
      margin-top: 40px; } }

.site-footer {
  padding-bottom: 100px; }

.site-footer-title {
  margin: 0;
  line-height: 1; }
  @media (max-width: 767px) {
    .site-footer-title {
      font-size: 21px; } }
  .site-footer-title + .site-footer-subtitle {
    margin-top: 20px; }

.site-footer-subtitle {
  font-weight: 300;
  margin: 0;
  line-height: 1.2; }
  @media (max-width: 767px) {
    .site-footer-subtitle {
      font-size: 14px; } }
